<template>
  <div id="map"></div>
</template>

<script>
import { initializeApp } from "firebase/app";
import { getFirestore, collection, getDocs, where, query } from 'firebase/firestore';
import L from 'leaflet'
import 'leaflet/dist/leaflet.css';
import { geocoder } from "leaflet-control-geocoder"

// Your web app's Firebase configuration
const firebaseConfig = {
  apiKey: "AIzaSyCVOhJEWMLfgI-wcNowIxo2YknhJB5c8LU",
  authDomain: "reserveio.firebaseapp.com",
  projectId: "reserveio",
  storageBucket: "reserveio.appspot.com",
  messagingSenderId: "666994813739",
  appId: "1:666994813739:web:6c2a95c15c639d3e73f36e",
  measurementId: "G-6H6FXQ5QXH"
};
  
// Initialize Firebase
const app = initializeApp(firebaseConfig);
const firestore = getFirestore(app);
export default {
  data() {
        return {
            map: null,
            marker: null,
            circle: null,
            lat: null,
            long: null,
            accuracy: null,
            predefinedPinCoordinates: [50.067168, 19.954201],
            predefinedPinMarker: null
        };
    },
    mounted() {
        this.initMap();
        this.addPredefinedPin();
        this.getCurrentPosition();

        setInterval(() => {
            this.getCurrentPosition();
        }, 5000);
    },
    methods: {
        initMap() {
            this.map = L.map('map', {
                center: [9.0820, 8.6753],
                zoom: 8
            });

            L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
                attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            }).addTo(this.map);

            L.Control.geocoder().addTo(this.map);
        },
        async addPredefinedPin() {
            if (this.predefinedPinMarker) {
                this.map.removeLayer(this.predefinedPinMarker);
            }
            const q = query(collection(firestore, "uslugodawcy"), where ("nazwa", "==", this.$route.params.providerName));
            const querySnapshot = await getDocs(q);
            var geopoint = [querySnapshot.docs[0].data().lokalizacja.latitude, querySnapshot.docs[0].data().lokalizacja.longitude]
            this.predefinedPinCoordinates = geopoint


            this.predefinedPinMarker = L.marker(this.predefinedPinCoordinates).addTo(this.map);

            if (this.marker) {
                const latlngs = [
                    this.predefinedPinCoordinates,
                    [this.marker.getLatLng().lat, this.marker.getLatLng().lng]
                ];
                const polyline = L.polyline(latlngs, { color: 'blue' }).addTo(this.map);
                this.map.fitBounds(polyline.getBounds());
            } else {
                this.map.setView(this.predefinedPinCoordinates, 8);
            }
        },
        getCurrentPosition() {
            if (!navigator.geolocation) {
                console.log("Your browser doesn't support geolocation feature!");
            } else {
                navigator.geolocation.getCurrentPosition(position => {
                    this.lat = position.coords.latitude;
                    this.long = position.coords.longitude;
                    this.accuracy = position.coords.accuracy;

                    if (this.marker) {
                        this.map.removeLayer(this.marker);
                    }

                    if (this.circle) {
                        this.map.removeLayer(this.circle);
                    }

                    this.marker = L.marker([this.lat, this.long]);
                    this.circle = L.circle([this.lat, this.long], { radius: this.accuracy });

                    const featureGroup = L.featureGroup([this.marker, this.circle]).addTo(this.map);

                    if (this.predefinedPinMarker) {
                        const latlngs = [
                            this.predefinedPinCoordinates,
                            [this.lat, this.long]
                        ];
                        const polyline = L.polyline(latlngs, { color: 'blue' }).addTo(this.map);
                        this.map.fitBounds(polyline.getBounds());
                    } else {
                        this.map.fitBounds(featureGroup.getBounds());
                    }
                });
            }
        }
    }
  }
</script>

<style>
  @import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
  @import url('https://fonts.googleapis.com/css2?family=Berkshire+Swash&display=swap');
  .card-header {
    text-align: center;
    float: center;
    font-size: 40px;
    font-family: 'Berkshire Swash';
  }
  .card-body {
    text-align: center;
    font-family: 'Lato', sans-serif;
    font-size: 30px;
  }
        * {
            margin: 0;
            padding: 0;
        }
        .imgbox {
            display: grid;
            height: 200px;
        }
        .center-fit {
            max-width: 200px;
            max-height: 100vh;
            margin: auto;
        }
        #map {
            width: 100%;
            height: 80vh;
        }
</style>